import React, { useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";
import IMAGES from "../../../assets/images";
import { BsArrowLeftShort } from "react-icons/bs";
import { useDispatch, useSelector } from "react-redux";

import { FieldError } from "../../../components/ErrorMessage/ErrorMessage";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";
import { useEffect } from "react";
import {
  confirmOtpByEmail,
  forgetPasswordReset,
  sendOtpToEmail,
} from "../../../app/features/auth/auth.slice";
import { Notification } from "../../../components/GenericActions";
import { ClipLoader } from "react-spinners";

export default function ForgetPassword() {
  const [confirmPassword, setConfirmPassword] = useState(0);
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [otp, setOtp] = useState(["", "", "", ""]);
  const [seconds, setSeconds] = useState(60);
  const [intervalId, setIntervalId] = useState(null);
  const [confirmOtpEmail, setConfirmOtpEmail] = useState("");
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleClickShowConfirmPassword = () =>
    setShowConfirmPassword((show) => !show);

  const { isLoading } = useSelector((state) => state.auth);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  function handleChange(event, index) {
    const value = event.target.value;
    const newOtp = [...otp];
    newOtp[index] = value.replace(/\D/, "");
    setOtp(newOtp);
    if (index < 5 && value) {
      document.getElementById(`otp${index + 1}`)?.focus();
    }
  }

  function handleKeyDown(event, index) {
    if (event.key === "Backspace" && !otp[index] && index > 0) {
      document.getElementById(`otp${index - 1}`)?.focus();
    }
  }

  useEffect(() => {
    const id = setInterval(() => {
      setSeconds((prevSeconds) => prevSeconds - 1);
    }, 1000);
    setIntervalId(id);
    return () => clearInterval(id);
  }, []);

  useEffect(() => {
    if (seconds === 0) {
      // eslint-disable-next-line
      clearInterval(intervalId);
    }
  }, [seconds, intervalId]);

  const moveToNext = (data) => {
    setConfirmPassword(1);
  };
  const moveToState2 = () => {
    setConfirmPassword(2);
  };
  const moveToState3 = () => {
    navigate(PATH.SIGNIN);
  };

  function onSubmit(data) {
    const sendOtpToEmailData = {
      email: data.email,
    };
    setConfirmOtpEmail(data.email);
    dispatch(sendOtpToEmail({ sendOtpToEmailData, moveToNext }));
  }

  function handleSubmitOtp(data) {
    const concatenatedOtp = data.otp0 + data.otp1 + data.otp2 + data.otp3;

    const confirmOtpByEmailData = {
      email: confirmOtpEmail,
      otp: concatenatedOtp,
      timeZone: "Asia/Karachi",
    };

    dispatch(confirmOtpByEmail({ confirmOtpByEmailData, moveToState2 }));
  }
  function handleConfirmPassword(data) {
    const finalData = {
      email: confirmOtpEmail,
      password: data.password && data.confirmOldPassword,
    };
    if (data.password === data.confirmOldPassword) {
      dispatch(forgetPasswordReset({ finalData, moveToState3 }));
    } else {
      Notification("Your Password doesn't Match!", "error");
    }
  }
  return (
    <div className="forget-component h-100">
      <Row className="vh-100 m-0">
        <Col sm={12} md={12} lg={7} className="p-0">
          <div className="bg-img2">
            <div className="signin-page">
              <div
                style={{
                  padding: "22% 44% 22% 9%",
                }}
              >
                <h4 className="heading1">GP care with a Modern Touch.</h4>

                <p className="heading2">
                  Fast, hassle-free healthcare for the most common medical
                  conditions from the comfort of your home.
                </p>
              </div>
            </div>
          </div>
        </Col>
        <Col
          sm={12}
          md={12}
          lg={5}
          className="d-flex align-items-center py-4 h-100 bg-white"
        >
          <div className="signin-content">
            <div className="d-flex justify-content-center w-100 my-4">
              <img src={IMAGES.LOGO} alt="FAM Doc Logo" className="img-fluid" />
            </div>
            <Card className="border-0 bg-transparent">
              <Card.Body>
                {confirmPassword === 0 ? (
                  <Form className="p-3" onSubmit={handleSubmit(onSubmit)}>
                    <div className="signin-heading text-center">
                      Forgot Password
                    </div>
                    <p className="text-center mb-5">
                      Enter your email we will send you a reset link
                    </p>
                    <Form.Group className="mb-3">
                      <Form.Label className="label-primary">Email</Form.Label>
                      <Form.Control
                        type="email"
                        name="email"
                        placeholder="Email"
                        size="lg"
                        {...register("email", { required: true })}
                      />
                      {errors.email && (
                        <FieldError message={"This Field is Required"} />
                      )}
                    </Form.Group>
                    <Button
                      className={`w-100 mt-4 ${isLoading && "disabled"}`}
                      type="submit"
                    >
                      {isLoading === true ? (
                        <ClipLoader
                          color="white"
                          size={25}
                          className="d-flex m-auto"
                        />
                      ) : (
                        "Send Link"
                      )}
                    </Button>
                    <div className="text-center pt-4">
                      <Link to={PATH.SIGNIN} className="back-sign-in">
                        <BsArrowLeftShort
                          className="align-items-center arrow-icon"
                          size={25}
                        />
                        Back To Sign In
                      </Link>
                    </div>
                  </Form>
                ) : (
                  ""
                )}
                {confirmPassword === 1 ? (
                  <form
                    className="p-3"
                    onSubmit={handleSubmit(handleSubmitOtp)}
                  >
                    <div className="signin-heading text-center">
                      Confirm Your OTP
                    </div>
                    <p className="text-center mb-5">
                      Please enter the code below weâ€™ve sent to your email
                    </p>
                    {otp.map((digit, index) => (
                      <input
                        key={index}
                        type="text"
                        id={`otp${index}`}
                        name={`otp${index}`}
                        maxLength={1}
                        value={digit}
                        {...register(`otp${index}`, { required: true })}
                        onChange={(event) => handleChange(event, index)}
                        onKeyDown={(event) => handleKeyDown(event, index)}
                        className="otp-field me-3 py-1 text-center"
                        style={{
                          width: "20%",
                          fontweight: 500,
                          fontSize: "31px",
                        }}
                      />
                    ))}
                    <p className="text-center pt-4 text-black">
                      <span className="text-gray text-cursor-pointer">
                        Resend
                      </span>{" "}
                      OTP in {seconds} Sec
                    </p>

                    <Button className="w-100 mt-5" type="submit">
                      Confirm Code
                    </Button>
                    <div className="text-center pt-4">
                      <Link to={PATH.SIGNIN} className="back-sign-in">
                        <BsArrowLeftShort
                          className="align-items-center arrow-icon"
                          size={25}
                        />
                        Back To Sign In
                      </Link>
                    </div>
                  </form>
                ) : (
                  ""
                )}
                {confirmPassword === 2 ? (
                  <Form
                    className="p-3"
                    onSubmit={handleSubmit(handleConfirmPassword)}
                  >
                    <div className="signin-heading text-center">
                      Create New Password
                    </div>
                    <p className="text-center mb-5">
                      Please create your new password for next time login
                    </p>
                    <Form.Group
                      className=" position-relative mb-3"
                      controlId="formBasicPassword"
                    >
                      <Form.Label className="label-primary">
                        Password
                      </Form.Label>
                      <Form.Control
                        type={showPassword ? "text" : "password"}
                        placeholder="Password"
                        name="password"
                        size="lg"
                        {...register("password", { required: true })}
                      />
                      <div
                        onClick={handleClickShowPassword}
                        className="eye-icon"
                      >
                        {showPassword ? (
                          <AiOutlineEye size={18} />
                        ) : (
                          <AiOutlineEyeInvisible size={18} />
                        )}
                      </div>
                    </Form.Group>
                    <Form.Group
                      className=" position-relative"
                      controlId="formBasicPassword1"
                    >
                      <Form.Label className="label-primary">
                        Confirm Password
                      </Form.Label>
                      <Form.Control
                        type={showConfirmPassword ? "text" : "password"}
                        placeholder="Password"
                        name="confirmOldPassword"
                        size="lg"
                        {...register("confirmOldPassword", { required: true })}
                      />
                      <div
                        onClick={handleClickShowConfirmPassword}
                        className="eye-icon"
                      >
                        {showConfirmPassword ? (
                          <AiOutlineEye size={18} />
                        ) : (
                          <AiOutlineEyeInvisible size={18} />
                        )}
                      </div>
                    </Form.Group>
                    <Button className="w-100 mt-4" type="submit">
                      Set Password
                    </Button>
                  </Form>
                ) : (
                  ""
                )}
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </div>
  );
}
